<template>
  <nav class="about-navbar" :class="{'about-navbar-active': burgerActiveForNav }">
    <div class="menu">
      <vs-button
          class="burger_menu"
          style="border-radius: 5px"
          upload
          icon
          color="#005C5F"
          @click="burgerActive"
      >
        <i v-if="burgerActiveForNav" class="el-icon-s-fold"></i>
        <i v-else class="el-icon-s-unfold"></i>
      </vs-button>
      <div class="logo" style="cursor: pointer" @click="$router.go(-1)">
        <div class="logo-title">
          <h2>MED <span style="color: #005C5F; text-transform: capitalize"> {{ clinics.title }}</span></h2>
          <p>медицинский комплекс</p>
        </div>
      </div>
    </div>
    <button class="clock">
      <div class="date" style="padding-right: 10px; margin-right: 10px; border-right: 1px solid black">
        <p style="font-size: 15px"> {{ dateNow }}.{{ monthNow }}.{{ yearNow }} </p>
        <p style="font-size: 15px">{{days[day]}}</p>
      </div>
      <div class="time">
        {{ hours }} : {{ minutes }} : {{ seconds }}
      </div>
    </button>
  </nav>
</template>

<script>
import {mapGetters, mapMutations} from "vuex";

export default {
  data() {
    return {
      hours: '00',
      minutes: '00',
      seconds: '00',
      dateNow: '00',
      monthNow: '00',
      yearNow: '0000',
      day: '0',
      days: ['воскресенье', 'понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота'],
    }
  },
  computed: {
    ...mapGetters(['clinics', 'burgerActiveForNav'])
  },
  methods: {
    ...mapMutations(['getClinics', 'burgerMenuActive']),
    burgerActive() {
      this.burgerMenuActive()
    }
  },
  mounted() {
    this.getClinics(this.$route.params.id)
    setInterval( () => {
      let date = new Date();
      this.hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      this.minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      this.seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      this.dateNow = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      this.monthNow = date.getMonth() < 10 ? '0' + date.getMonth() : date.getMonth()
      this.day = date.getDay()
      this.yearNow = date.getFullYear()
      this.formDateNow = `${this.dateNow}-${this.monthNow}-${this.yearNow}/${this.hours}:${this.minutes}`
    }, 1000)
  }
}
</script>

<style scoped>
.about-navbar {
  padding: 10px 30px 10px 270px;
  background: white;
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
  transition: .5s;
}

.burger_menu {
  display: none;
}

.clock {
  padding: 20px 30px;
  font-size: 30px;
  display: flex;
  align-items: center;
  background: none;
  outline: none;
  border: unset;
}
</style>