<template>
<div>
  <about-nav-bar/>
  <div class="specialist" :class="{'specialist-active': burgerActiveForNav}">
    <header class="specialist-header">
      <img :src="aboutSpecialist.photo" alt="image">
      <div class="feedback-form">
        <h2 class="specialist-name">{{ aboutSpecialist.full_name }}</h2>
        <p class="specialist-job">{{ aboutSpecialist.specialization }}</p>
        <form @submit.prevent="postFeedback" style="display:flex; flex-direction: column; gap: 30px">
          <vs-input
              :state="(!$v.fullName.$dirty && !$v.fullName.required) || (!$v.email.$dirty  && !$v.fullName.minLength) ? 'danger' : 'success'"
              v-model.trim="fullName"
              placeholder="Имя"
          >
            <template #icon>
              <i style="color: grey" class='el-icon-user'></i>
            </template>
            <template #message-danger v-if="!$v.fullName.$dirty && !$v.fullName.required">
              Введите имя
            </template>
            <template #message-danger v-else-if="!$v.fullName.$dirty && !$v.fullName.minLength">
              Длина имени должна быть больше чем 6 знаков, <br> длина сейчас {{fullName.length}}
            </template>
            <template #message-success v-else-if="(!(!$v.fullName.$dirty && !$v.fullName.required) || (!$v.fullName.$dirty  && !$v.fullName.minLength))">
              Имя введено правильно
            </template>
          </vs-input>
          <vs-input :state="(!$v.email.$dirty && !$v.email.email) || (!$v.email.$dirty && !$v.email.required) ? 'danger' : 'success'" v-model="email" placeholder="Email">
            <template #icon>
              <i style="color: grey" class='el-icon-eleme'></i>
            </template>
            <template #message-danger v-if="!$v.email.$dirty && !$v.email.required">
              Поля email не должно быть пустым
            </template>
            <template #message-danger v-else-if="!$v.email.$dirty && !$v.email.email">
              Введите правильный email
            </template>
            <template #message-success v-else-if="(!(!$v.email.$dirty && !$v.email.email) || (!$v.email.$dirty && !$v.email.required))">
              Электронная почта действительна
            </template>
          </vs-input>
          <vs-input v-model="message" placeholder="Сообщение" :state="(!$v.message.$dirty && !$v.message.required) || (!$v.message.$dirty  && !$v.message.minLength) ? 'danger' : 'success'">
            <template #icon>
              <i style="color: grey" class='el-icon-s-promotion'></i>
            </template>
            <template #message-danger v-if="!$v.message.$dirty && !$v.message.required">
              Введите отзыв
            </template>
            <template #message-danger v-else-if="!$v.message.$dirty && !$v.message.minLength">
              Длина отзыва должна быть больше чем 10 знаков, <br> длина сейчас {{message.length}}
            </template>
            <template #message-success v-else-if="(!(!$v.message.$dirty && !$v.message.required) || (!$v.message.$dirty  && !$v.message.minLength))">
              Отзыв введено правильно
            </template>
          </vs-input>
          <vs-button
              type="submit"
              style="width: 180px; margin-top: 10px" color="#005C5F">
            Отправить отзыв
          </vs-button>
        </form>
      </div>
    </header>
    <section class="section-details">
      <div class="experience">
        <h3>Опыт работы — {{ aboutSpecialist.experience }} лет</h3>
        <div class="about-experience">
          <div>
            <h4>Профессиональные навыки:</h4>
            <div>
              <p style="padding-bottom: 10px" v-for="skill in aboutSpecialist.skills">
                <i class="el-icon-circle-check"></i>
                <span style="font-size: 18px; color: #7a7878"> {{ skill }} </span>
              </p>
            </div>
          </div>
          <div>
            <h4>Основное образование:</h4>
            <div>
              <p style="padding-bottom: 10px" v-for="education in aboutSpecialist.education">
                <i class="el-icon-zoom-in"></i>
                <span style="font-size: 17px; color: #7a7878">{{ education.finished }} - {{ education.institution }} <<{{ education.speciality }}>></span>
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="feedback">
        <h3>Отзывы о враче</h3>
        <div class="feedbacks" v-for="feedback in feedbacks">
          <div class="feedbacks_img">
            <img style="width: 100px; height: 100px" src="../assets/icon/comment.png" alt="comment">
          </div>
          <div style="display:flex; justify-content: space-between; flex-wrap: wrap; ">
            <div style="display:flex; flex-direction: column; align-items: flex-start; max-width: 900px; width: 100%;">
              <h3 class="comment_name" style="font-size: 20px; color: black; text-transform: capitalize">
                {{ feedback.full_name }}
                <span style="font-size: 16px; font-weight: normal;">
                  {{ feedback.created_at[0]+
                feedback.created_at[1]+
                feedback.created_at[2]+
                feedback.created_at[3]+
                feedback.created_at[4]+
                feedback.created_at[5]+
                feedback.created_at[6]+
                feedback.created_at[7]+
                feedback.created_at[8]+
                feedback.created_at[9]
                  }}
                </span>
              </h3>
              <p class="comment_text" style="font-size: 18px; font-weight: lighter; color: grey; line-height: 130%; letter-spacing: 1px">
                {{ feedback.message }}
              </p>
            </div>
            <div style="display:flex; flex-direction: column; align-items: flex-start">
              <h3 style="font-size: 20px;">Email:</h3>
              <p>{{ feedback.email }}</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
  <footer class="footer" :class="{'footer-active': burgerActiveForNav}">
    <div class="footer-logo">
      <h3>MED <span style="color: #005C5F;">{{clinics.title}}</span></h3>
      <p>медицинский комплекс</p>
    </div>
    <div style="display:flex; flex-direction: column; gap: 10px">
      <h5>{{ aboutSpecialist.full_name }}</h5>
      <a :href="'tel:'+ aboutSpecialist.contact">
        <i class="el-icon-mobile"></i>
        {{ aboutSpecialist.contact }}
      </a>
    </div>
  </footer>
</div>
</template>
<script>
import axios from "axios";
import AboutNavBar from "@/components/AboutNavBar";
import { email, required, minLength, maxLength } from 'vuelidate/lib/validators'
import {mapGetters, mapMutations} from "vuex";

export default {
  components: { AboutNavBar },
  data() {
    return {
      fullName: '',
      email: '',
      message: '',
      offset: null
    }
  },
  validations: {
    fullName: {required, minLength: minLength(4)},
    email: {email, required},
    message: {required, minLength: minLength(10)},
  },
  methods: {
    ...mapMutations(['getFeedbacks', 'getClinics', 'getSpecialist']),
    postFeedback() {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      axios.post(`http://18.206.211.243/feedbacks/`, {
        full_name: this.fullName,
        email: this.email,
        message: this.message,
        specialist: this.$route.params.id
      })
      .then(res => {
        this.$message({
          message: 'Ваш отзыв получен, спасибо',
          type: 'success'
        });
        this.fullName = ''
        this.email = ''
        this.message = ''
      })
    }
  },
  computed: {
    ...mapGetters(['feedbacks', 'aboutSpecialist', 'burgerActiveForNav', 'clinics'])
  },
  mounted() {
    this.getFeedbacks(this.$route.params.id)
    this.getSpecialist(this.$route.params.id)
  }
}
</script>

<style scoped>

.footer div h5 {
  font-size: 18px;
  color: white;
}

.footer div a {
  color: white;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.footer div a i{
  color: white;
  font-size: 30px;
}

.footer-logo h3 {
  font-size: 24px;
  color: white;
}

.footer-logo p {
  color: white;
}

.footer-logo {
  display: flex;
  flex-direction: column;
}

.footer {
  width: 100%;
  padding: 30px 20px 30px 270px;
  background: #3d3d3d;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: .5s;
}

.feedback-form p {
  margin-bottom: 20px;
}

.feedback-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
}

.feedbacks {
  padding: 40px 100px;
  border-top: 1px solid rgba(122, 120, 120, 0.34);
  border-bottom: 1px solid rgba(122, 120, 120, 0.34);
  position: relative;
}

.feedbacks_img img {
  left: 30px;
  opacity: .3;
  position: absolute;
  z-index: -1;
}

.feedback {
  margin-bottom: 30px;
}

.about-experience div div p{
  display: flex;
  align-items: center;
  gap: 30px;
}

.about-experience div div i {
  color: #005C5F;
  font-size: 30px;
}

.about-experience div h4 {
  margin: 1.5em 0 0.75em;
  padding: 0;
  font-size: 22px;
  line-height: 1.36;
  font-weight: 700;
}

.about-experience div {
  width: 600px;
}

.feedback h3,
.experience h3 {
  color: rgb(0, 92, 95);
  padding: 30px 0;
  font-size: 40px;
  text-align: center;
}

.experience {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.about-experience {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.section-details {
  padding-top: 60px;
}

.form-label i{
  font-size: 25px;
  color: grey;
}

.form h4 {
  font-size: 35px;
  line-height: 1.14;
  font-family: Affect,Helvetica,Arial,sans-serif;
  font-weight: 700;
  color: #000;
  max-width: 230px
}

.form span {
  margin: 0;
  font-size: 13px;
  color: #7a7878;
}


.specialist-name {
  font-size: 60px;
  text-transform: lowercase;
  margin-top: 0.4em;
  margin-left: -2px;
  max-width: 500px;
  color: #3d3d3d;
}

.specialist-job {
  text-transform: capitalize;
  color: #3d3d3d;
  font-size: 18px;
  margin: 1.5em 0 0;
  max-width: 400px;
  width: 100%;
}

.specialist-header {
  max-width: 1600px;
  width: 100%;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.specialist-header img {
  height: 600px;
}

.specialist {
  padding: 115px 50px 0 270px;
  transition: .5s;
}

</style>